define('ember-htmlbars/hooks/content', ['exports', 'ember-views/views/simple_bound_view', 'ember-metal/streams/utils', 'ember-htmlbars/system/lookup-helper'], function (exports, simple_bound_view, utils, lookupHelper) {

  'use strict';



  exports['default'] = content;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function content(env, morph, view, path) {
    var helper = lookupHelper['default'](path, view, env);
    var result;

    if (helper) {
      var options = {
        morph: morph,
        isInline: true
      };
      result = helper.helperFunction.call(undefined, [], {}, options, env);
    } else {
      result = view.getStream(path);
    }

    if (utils.isStream(result)) {
      simple_bound_view.appendSimpleBoundView(view, morph, result);
    } else {
      morph.setContent(result);
    }
  }

});