define('ember-htmlbars/hooks/element', ['exports', 'ember-metal/core', 'ember-metal/streams/utils', 'ember-htmlbars/system/lookup-helper'], function (exports, Ember, utils, lookupHelper) {

  'use strict';



  exports['default'] = element;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  var fakeElement;

  function updateElementAttributesFromString(dom, element, string) {
    if (!fakeElement) {
      fakeElement = document.createElement('div');
    }

    fakeElement.innerHTML = '<' + element.tagName + ' ' + string + '><' + '/' + element.tagName + '>';

    var attrs = fakeElement.firstChild.attributes;
    for (var i = 0, l = attrs.length; i < l; i++) {
      var attr = attrs[i];
      if (attr.specified) {
        dom.setAttribute(element, attr.name, attr.value);
      }
    }
  }
  function element(env, domElement, view, path, params, hash) {
    //jshint ignore:line
    var helper = lookupHelper['default'](path, view, env);
    var valueOrLazyValue;

    if (helper) {
      var options = {
        element: domElement
      };
      valueOrLazyValue = helper.helperFunction.call(undefined, params, hash, options, env);
    } else {
      valueOrLazyValue = view.getStream(path);
    }

    var value = utils.read(valueOrLazyValue);
    if (value) {
            updateElementAttributesFromString(env.dom, domElement, value);
    }
  }

});