define('ember-htmlbars/hooks/block', ['exports', 'ember-views/views/simple_bound_view', 'ember-metal/streams/utils', 'ember-htmlbars/system/lookup-helper'], function (exports, simple_bound_view, utils, lookupHelper) {

  'use strict';



  exports['default'] = block;
  /**
  @module ember
  @submodule ember-htmlbars
  */

  function block(env, morph, view, path, params, hash, template, inverse) {
    var helper = lookupHelper['default'](path, view, env);

    
    var options = {
      morph: morph,
      template: template,
      inverse: inverse,
      isBlock: true
    };
    var result = helper.helperFunction.call(undefined, params, hash, options, env);

    if (utils.isStream(result)) {
      simple_bound_view.appendSimpleBoundView(view, morph, result);
    } else {
      morph.setContent(result);
    }
  }

});