define('ember-htmlbars/helpers/input', ['exports', 'ember-views/views/checkbox', 'ember-views/views/text_field', 'ember-metal/streams/utils', 'ember-metal/core'], function (exports, Checkbox, TextField, utils, Ember) {

  'use strict';

  exports.inputHelper = inputHelper;

  function inputHelper(params, hash, options, env) {
    
    var onEvent = hash.on;
    var inputType;

    inputType = utils.read(hash.type);

    if (inputType === 'checkbox') {
      delete hash.type;

      
      env.helpers.view.helperFunction.call(this, [Checkbox['default']], hash, options, env);
    } else {
      delete hash.on;

      hash.onEvent = onEvent || 'enter';
      env.helpers.view.helperFunction.call(this, [TextField['default']], hash, options, env);
    }
  }

});